import React, { useState } from "react";
import { Link , NavLink } from "react-router-dom";
import logo from "../../imgs/logo.png";

const Header = () => {
  const [toggle, setToggle] = useState(false);
  const Humberger = () => (
    <div
      id={`${toggle ? "open-humberger" : ""}`}
      className={`navigation__humberger `}
      onClick={() => setToggle(!toggle)}
    >
      <span></span>
      <span></span>
      <span></span>
    </div>
  );
  const NavList = () => (
    <>
      <ul className={`navigation__list ${toggle && "open"}`}>
        <li className="navigation-item">
          <NavLink exact to="/" className="navigation__list--link ">
            home
          </NavLink>
        </li>
        <li className="navigation-item">
        <Link to="/#projects" className="navigation__list--link ">
            projects
          </Link>
        </li>
        <li className="navigation-item">
        <NavLink to="/contact" className="navigation__list--link ">
            contact
          </NavLink>
        </li>
      </ul>
    </>
  );
  return (
    <header className="header">
      <div className="header__logo">
        <img src={logo} alt="logo" className="header__logo-logo" />
      </div>
      <nav className="navigation">
        <NavList />
      </nav>
      <Humberger />
    </header>
  );
};

export default Header;
